import { reactive, ref } from 'vue'
import { isFunction } from 'utils/is'
import store from 'store'

const useBankTransferSetting = () => {
	const data = reactive({
		bank_transfer: []
	})
  const modal = ref(false)
  const loading = reactive({
    show: false,
    update: false
  })
  const actionShow = async (callback = () => {}) => {
    try{
      loading.show = true
      const response = await store.dispatch(`setting/bankTransfer/show`)
      data.bank_transfer = response
      isFunction(callback) && callback(null, null)
    }catch(e){
      isFunction(callback) && callback(e, null)
    }finally{
      loading.show = false
    }
  }
  const actionUpdate = async (callback = () => {}) => {
    try{
      loading.update = true
      await store.dispatch(`setting/bankTransfer/update`, data)
      isFunction(callback) && callback(null, null)
    }catch(e){
      isFunction(callback) && callback(e, null)
    }finally{
      loading.update = false
    }
  }
  const unsetData = () => {}
  const openModal = async () => {
    modal.value = true
    await actionShow()
  }
  const closeModal = () => (modal.value = false)
  return {
		data,
    loading,
    modal,
    actionShow,
    actionUpdate,
    openModal,
    closeModal,
		unsetData
  }
}

const usePayoutCustom = () => {
  const loading = ref(false)

  const create = async (payload, callback = () => {}) => {
    const formData = new FormData()

    Object.keys(payload).forEach(key => {
      if (key === 'fields') {
        formData.append(key, JSON.stringify(payload.fields))
      } else {
        formData.append(key, payload[key])
      }
    })

    try {
      loading.value = true
      await store.dispatch(`setting/paymentMethod/createCustom`, formData)
      isFunction(callback) && callback()
    } catch(e) {
      isFunction(callback) && callback(e, null)
    }finally{
      loading.value = false
    }
  }

  const update = async (id, payload, callback = () => {}) => {
    const formData = new FormData()

    Object.keys(payload).forEach(key => {
      if (key === 'fields') {
        formData.append(key, JSON.stringify(payload.fields))
      } else {
        formData.append(key, payload[key])
      }
    })
    formData.append('_method', 'PUT')

    try {
      loading.value = true
      await store.dispatch(`setting/paymentMethod/updateCustom`, {
        id,
        data: formData
      })
      isFunction(callback) && callback()
    } catch(e) {
      isFunction(callback) && callback(e, null)
    }finally{
      loading.value = false
    }
  }

  const trash = async (id, callback = () => {}) => {
    try {
      loading.value = true
      await store.dispatch(`setting/paymentMethod/deleteCustom`, id)
      isFunction(callback) && callback()
    } catch(e) {
      isFunction(callback) && callback(e, null)
    }finally{
      loading.value = false
    }
  }

  return {
    loading,
    create,
    update,
    trash
  }
}

export { useBankTransferSetting, usePayoutCustom }
