<template>
  <div class="space-y-8">
    <div>
      <Input
        v-model="state.name"
        label="Payout Name"
        placeholder="Custom Payout Name"
        class="w-full"
      />
      <small
        v-for="(error, errorIndex) of validation.name.$errors"
        :key="errorIndex"
        class="mt-2 text-red-base block">
        {{ error.$message }}.
      </small>      
    </div>
    <div>
      <Button
        v-if="!state.fields.length"
        color="teal"
        dense
        @click="handleAddInput">
        Add Input Field
      </Button>
      <div
        v-else
        v-for="(field, nfield) in state.fields"
        :key="nfield">
        <label for="custom_field_name">Field Name</label>
        <div class="flex items-center gap-x-4">
          <Input
            v-model="field.name"
            id="custom_field_name"
            placeholder="Custom Field Name"
            class="w-full"
            :view="validation.fields[nfield]?.name?.$error ? 'danger' : ''"
          /> 
          <Button color="red" @click="handleDeleteInput(nfield)">
            <span class="material-icons">delete</span>
          </Button>
        </div>
        <small
          v-for="(error, errorIndex) of validation.fields[nfield]?.name?.$errors || []"
          :key="errorIndex"
          class="mt-2 text-red-base block">
          {{ error.$message }}.
        </small> 
      </div>
    </div>
    <div>
      <Fieldset class="rounded-md">
        <template #label>
          Upload Icon
        </template>
        <template #default>
          <div class="max-w-md">
            <InputFile
              :file="state.icon"
              :action="false"
              :acceptFile="'image/svg+xml'"
              preview
              @input="handleFile">
              <template #description>
                <small
                  v-for="(error, errorIndex) of validation.icon.$errors"
                  :key="errorIndex"
                  class="mt-2 text-red-base block">
                  {{ error.$message }}.
                </small>
                <small>
                  <b>Suggested</b>: 56x56px ;
                  <b>File</b>: SVG ;
                  <b>Max</b> 250KB
                </small>
              </template>
            </InputFile>
          </div>
        </template>
      </Fieldset>
    </div>
  </div>
</template>

<script>
import { computed, defineComponent, reactive, watch } from 'vue'
import { useVuelidate } from '@vuelidate/core'
import { required, maxLength, minLength, fileSize, fileType } from '@/validations/index'
import { helpers } from '@vuelidate/validators'

import Fieldset from 'bbUi/components/fieldset/fieldset.vue'
import Button from 'bbUi/components/button/button.vue'
import Input from 'bbUi/components/input/input.vue'
import InputFile from 'components/InputFile.vue'

export default defineComponent({
  name: 'CustomPayoutForm',
  components: {
    Input,
    Button,
    Fieldset,
    InputFile
  },
  props: {
    data: {
      type: Object,
      default: undefined
    },
    edit: {
      type: Boolean,
      default: false
    }
  },
  setup(props) {
    const state = reactive({
      name: null,
      icon: null,
      fields: [{ name: null, type: 'text' }]
    })
    const fieldRules = computed(() => {
      return state.fields.map(() => ({
        name: {
          required: helpers.withMessage('Field name is required', required)
        }
      })) ?? {}
    })
    const rules = computed(() => ({
      name: { required, maxLength: maxLength(32), minLength: minLength(3) },
      icon: { required, fileSize: fileSize(256), fileType: fileType('image/svg+xml') },
      fields: fieldRules.value
    }))
    const validation = useVuelidate(rules, state, {
      $lazy: true
    })
    const handleFile = (e) => {
      state.icon = e
    }
    const handleAddInput = () => {
      validation.value.fields?.$reset()
      state.fields.push({
        name: null,
        type: "text"
      })
    }
    const handleDeleteInput = (n) => {
      validation.value.fields?.$reset()
      state.fields.splice(n, 1)
    }
    const handleSubmit = async () => {
      const valid = await validation.value.$validate()
      if (valid) {
        let fields = []
        if (state.fields && state.fields.length) {
          fields = (typeof state.fields !== String && [...state.fields].filter(field => field.name && field.name.length)) || []
        }

        return {
          name: state.name,
          icon: state.icon,
          fields
        }
      }

      return undefined
    }

    watch(() => props.data, (val) => {
      if (props.edit && val && Object.keys(val).length) {
        // EDIT MODE
        state.name = val.name
        state.fields = []

        if (val.attributes && val.attributes.length) {
          val.attributes.forEach(element => {
            state.fields.push({
              name: element.name,
              type: element.type
            })
          })
        }

        state.icon = val.icon
      } else if (!props.edit) {
        // CREATE MODE
        state.name = null
        state.icon = null
        state.fields = [{ name: null, type: 'text' }]
      }
    }, {
      immediate: true,
      deep: true
    })

    return {
      state,
      validation,
      handleFile,
      handleAddInput,
      handleDeleteInput,
      handleSubmit
    }
  },
})
</script>
