<template>
  <div
    :class="[
      'custom-payout-trigger',
      disabled && 'custom-payout-trigger--disabled'
    ]">
    <div v-if="disabled" class="custom-payout-trigger--tooltip">
      <div class="custom-payout-trigger--tooltip__content">
        You are limited to having only one custom payout method.
      </div>
    </div>
    <div class="custom-payout-trigger--btn">
      <Button
        :color="trash ? 'red' : 'teal'"
        :disabled="disabled"
        :dense="edit || trash"
        :view="!edit || !trash ? 'outlined' : 'filled'"
        @click="show = true">
        <template v-if="!$slots.default">
          <div class="flex items-center justify-center gap-x-1">
            <span class="material-icons">{{ icon || (edit ? 'mode_edit' : trash ? 'delete' : 'add') }}</span>
            {{ label || '' }}
          </div>
        </template>
        <template v-else>
          <slot />
        </template>
      </Button>
    </div>
  </div>
  <Teleport to="body">
    <Modal
      v-model="show"
      :hide-header="trash"
      :size="trash ? 'xs' : 'sm'"
      title="Custom Payout">
      <template #default>
        <div class="grid md:grid-cols-1 grid-cols-1 gap-x-4">
          <CustomPayoutForm
            v-if="!trash"
            :data="data"
            :edit="data?.id ? true : false"
            v-loading="loading"
            ref="customPayoutForm"
          />
          <template v-if="trash">
            <div v-loading="loading" class="flex flex-col justify-center items-center gap-y-4">
              <p class="text-red-04">
                <span
                  class="material-icons"
                  :style="{
                    fontSize: '10rem !important',
                    lineHeight: '10rem'
                  }">
                  error_outline
                </span>
              </p>
              <p class="text-xl font-bold">
                Delete {{ data.name }} Payout Method?
              </p>
              <p class="w-96 text-sm text-center text-grey-02">
                Removing this payout method will not delete any existing offers associated with it
              </p>
              <div class="flex items-center justify-center gap-x-4">
                <Button
                  :loading="loading"
                  color="red"
                  @click="handleDelete">
                  Delete
                </Button>
                <div
                  class="hover:bg-grey-01 px-4 py-4 rounded-xl cursor-pointer"
                  @click="show = false">
                  Cancel
                </div>
              </div>
            </div>
          </template>
          <!-- <CustomPayoutPreview /> -->
        </div>
      </template>
      <template #footer>
        <Button
          v-if="!trash"
          :loading="loading"
          color="teal"
          @click="handleSave">
          Save Changes
        </Button>
      </template>
    </Modal>
  </Teleport>
</template>

<script>
import { defineComponent, ref, computed } from 'vue'
import { usePayoutCustom } from 'composable/setting/payment'

import Modal from 'bbUi/components/modal/modal.vue'
import Button from 'bbUi/components/button/button.vue'
import CustomPayoutForm from './Form.vue'
import CustomPayoutPreview from './Preview.vue'

export default defineComponent({
  name: 'CustomPayout',
  props: {
    data: {
      type: Object,
      default: undefined
    },
    label: {
      type: String,
      default: undefined
    },
    icon: {
      type: String,
      default: undefined
    },
    edit: {
      type: Boolean,
      default: false
    },
    trash: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  components: {
    Modal,
    Button,
    CustomPayoutForm,
    CustomPayoutPreview
  },
  emits: [ 'create', 'update', 'delete', 'update:modelValue' ],
  setup(props, { emit }) {
    const show = ref(false)
    const data = computed(() => (props.data || {}))
    const customPayoutForm = ref()
    const { loading, create, update, trash } = usePayoutCustom()

    const handleSave = async () => {
      const payload = await customPayoutForm.value.handleSubmit()
      if (payload) {
        if (props.edit) {
          return update(data.value.id, payload, () => {
            show.value = false
            emit('update')
          })
        } else {
          return await create(payload, () => {
            show.value = false
            emit('create')
          })
        }
      }
    }

    const handleDelete = async () => {
      return await trash(data.value.id, () => {
        show.value = false
        emit('delete')
      })
    }

    return {
      customPayoutForm,
      handleDelete,
      handleSave,
      loading,
      create,
      show,
      data
    }
  },
})
</script>

<style lang="scss">
.custom-payout-trigger {
  @apply relative;

  .custom-payout-trigger--tooltip {
    @apply absolute bottom-full pb-2 left-1/2;

    transform: translateX(-50%);

    .custom-payout-trigger--tooltip__content {
      @apply bg-teal-base text-white rounded-md px-4 py-2 text-sm w-60 shadow opacity-0 hidden transition-all ease-out duration-300;

      min-width: 120px;
    }
  }

  &--disabled {
    @apply cursor-pointer;

    &:hover {
      .custom-payout-trigger--tooltip {
        .custom-payout-trigger--tooltip__content {
          @apply block opacity-100;
        }
      }
    }
  }
}
</style>
